@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
	margin:0;
	font-family: 'Montserrat', sans-serif;
	padding:0;
}
.App{
    height: 100%;
	background-color: #FFF;
}
.perm-nav{
	box-sizing: border-box;
	position: fixed;
	/* z-index: 1;
	left:0; */
	overflow: hidden;
	width:20vw;
	background-color: white;
	color:darkslategrey;
	padding:3em 2em;
	height: 100vh;
	overflow-y: hidden;
	box-shadow: 13px 0px 18px -8px rgba(0,0,0,0.3);
}

.user-session{
	text-align: center;
	margin-bottom: 8em;
	font-size:0.7em;
}
.user-session > *{
	padding:2%;
}
#logout{
	background-color: white;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	border:none;
	margin-top:3%;
	font-weight: bolder;
	padding:4% 7%;
}
.nav{
	padding-left: 16%;
}
.nav-links{
	text-align: left;
	font-weight: bold;
}
.active{
    color: black!important;
    font-weight: bolder!important;
}
.nav-links a{
	padding:6% 4%;
	font-size:0.7em;
	display: block;
	text-decoration: none;
    color: lightgray;
    font-weight: bolder;
}
.ficon{
	margin-right:10%; 
	font-size: 1.1em;
}

#nav-security{
	color:darkred;
	margin-top:20px;
}
#nav-security .ficon{
	color:black;
}
.para{
    font-size:0.6em;
    padding:3%;
    margin: -1% 14%;
}
.f4.para{
	padding:3%;
    margin:3% 14%;
    font-weight: bold;
}


#nav-store{
	margin-top: 18%;
	padding-bottom: 5px;
	font-weight: 900;
}
#nav-store>.ficon{
	font-size: 1.1em;;
}

.card{
    width:30%;
}
/* .card-list::-webkit-scrollbar{
    width: 1em;
} */
.dashboard{
    width:100%;
}
.card-list{
    overflow-x:auto;
    width: auto;
}

.card-list::-webkit-scrollbar{
    height: 0.4em;
}

.recent-cover{
    margin-bottom:0.6em;
}
.card-list::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}

.card-list::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #AAA;
    height: 5em;
    /* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);  */
}

.list-title{
    font-size: 1.2em;
    font-weight: 200;
}
table{
    table-layout: fixed;
}
table,tr{
    width:100%;
    display: table;
    font-size: 0.7em;
    /* margin:1em; */
    border-collapse: collapse;
}
td,th{
    padding: 1.5em;
}
td,th{
    border:1px lightgray solid;
    width:19%;
}
.table-func{
    width:5em;
}

.func-div>div{
    padding: 0 1.9em;
}
th{
    font-size: 0.6em;
}
td{
    font-size: 1.4em;
    font-weight: 600;
}
.add-submit{
    background: none;
    border:none;
    font-size: 0.8em;
}

.add-text{
    padding: 1.25em;
    font-size:0.7em;
    font-weight: 800;
}
.add-text::-webkit-input-placeholder{
    font-weight: 200;
}
.add-text::-moz-placeholder{
    font-weight: 200;
}
.add-text:-ms-input-placeholder{
    font-weight: 200;
}
.add-text::-ms-input-placeholder{
    font-weight: 200;
}
.add-text::placeholder{
    font-weight: 200;
}
.add-tag{
    padding: 0.8em;
    font-size:0.8em;
    background-color: #DC143C;
    color: white;
    font-weight: 00;
}

.add-tag::-webkit-input-placeholder{
    color: whitesmoke;
    font-weight: 300;
}

.add-tag::-moz-placeholder{
    color: whitesmoke;
    font-weight: 300;
}

.add-tag:-ms-input-placeholder{
    color: whitesmoke;
    font-weight: 300;
}

.add-tag::-ms-input-placeholder{
    color: whitesmoke;
    font-weight: 300;
}

.add-tag::placeholder{
    color: whitesmoke;
    font-weight: 300;
}

.right-div{
  box-sizing: border-box;
  width: 75vw;
  float: left;
  margin-left:22%;
  z-index: 1;
  overflow: hidden;
  overflow-x: hidden;
}
